.flex-center {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

header {
  padding: 20px;
  box-shadow: 0 0 8px #d8d8d8;
}

header ul.mainmenu {
  display: flex;
}

header ul.mainmenu li {
  display: flex;
  align-items: center;
}

header ul.mainmenu li:before {
  content: "";
  width: 4px;
  height: 4px;
  background: #256d33;
  display: inline-block;
  border-radius: 50%;
}

header ul.mainmenu li:first-child:before {
  display: none;
}

header ul.mainmenu li a {
  font-family: "NotoSans", sans-serif;
  text-decoration: none;
  font-size: 12pt;
  color: #248100;
  padding: 14px;
  transition: 0.2s ease;
}

header ul.mainmenu li a:hover {
  color: #42a940;
}

.container .logo {
  width: 343px;
  height: 68px;
  background: url(../img/logo.png) no-repeat;
  text-indent: -9999px;
  transition: 0.2s ease;
}

.container .logo:hover {
  filter: brightness(1.15);
}

.slide-inside {
  height: 60px !important;
}

.slider {
  background: url(../img/bg.jpg);
  height: 415px;
  background-size: cover;
  position: relative;
  overflow: hidden;
}

.slider .card {
  width: 233px;
  height: 279px;
  position: relative;
  margin: -11.5px 0 10px 10px;
  transform: scale(0.55) rotate(-36deg);
  z-index: -1;
}

.slider .card:last-child {
  margin: -195.5px 0 9px -60px;
  transform: scale(0.55) rotate(-78deg);
}

.slider .card .photo {
  width: 207px;
  height: 220px;
  margin: 12px;
  background-size: cover!important;
  z-index: 4;
  position: relative;
  cursor: pointer;
}

.slider .card:before {
  background: url(../img/frame.png) no-repeat;
  content: "";
  width: 100%;
  height: inherit;
  display: block;
  position: absolute;
  z-index: 5;
  pointer-events: none;
}

.slider .flex-slide {
  height: 415px;
  align-items: center;
  display: flex;
  padding: 0 70px;
  box-sizing: border-box;
  justify-content: space-between;
}

.slider .flex-slide .right-img {
  width: 490px;
  height: 450px;
  background: url(../img/sprite.png) -147px 0 no-repeat;
  margin-right: -145px;
  margin-top: -12px;
}

.slider .flex-slide .text span:first-child {
  font-family: "NotoSans", sans-serif;
  font-size: 24pt;
  color: #fff;
  display: block;
}

.slider .flex-slide .text span:first-child:first-letter {
  color: #1d6d2a !important;
  background: #fff;
  padding: 3px 5px 0 7px;
  display: inline-block;
  position: relative;
  margin-right: 3px;
}

.slider .flex-slide .text span:last-child {
  font-family: columbina, sans-serif;
  color: #fff;
  font-size: 45pt;
  width: 470px;
  display: inline-block;
  line-height: 60px;
}

.slider:before {
  background: #1a732b;
  opacity: 0.82;
  display: block;
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
}

.slider .container {
  z-index: 1;
  position: relative;
}

.photoconsurs h2 {
  text-align: center;
  font-family: "columbina", sans-serif;
  font-size: 30pt;
  margin: 24px 0 19px;
  color: #256d33;
}

.photoconsurs .photos {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-evenly;
}

.photoconsurs .photos .morecards {
  width: 233px;
  height: 279px;
  background: #277b26;
  padding: 20px;
  box-sizing: border-box;
  font-family: "NotoSans", sans-serif;
  color: #fff;
  text-decoration: none;
  font-size: 22pt;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  transition: 0.2s ease;
  margin: 11.5px;
}

.photoconsurs .photos .morecards span:last-child {
  font-family: "Calibri", sans-serif;
  font-size: 13pt;
  font-style: italic;
  margin-top: 15px;
  color: #baeab9;
}

.photoconsurs .photos .morecards span:first-child {
  width: 75px;
  height: 75px;
  border-radius: 50%;
  background: #42a940;
  text-align: center;
  font-size: 58pt;
  margin-bottom: 26px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.photoconsurs .photos .morecards:hover {
  filter: brightness(1.15);
}

.photoconsurs .photos .card {
  width: 233px;
  height: 279px;
  position: relative;
  margin: 11.5px;
}

.photoconsurs .photos .card .photo {
  width: 207px;
  height: 208px;
  margin: 12px;
  background-size: cover!important;
  z-index: 4;
  position: relative;
  cursor: pointer;
  display: block;
}

.photoconsurs .photos .card:before {
  background: url(../img/frame.png) no-repeat;
  content: "";
  width: 100%;
  height: inherit;
  display: block;
  position: absolute;
  z-index: 5;
  pointer-events: none;
}

.weather {
  background: url(../img/weather/sun.jpg) center;
  background-size: cover;
  font-family: "NotoSans", sans-serif;
  color: #fff;
  text-shadow: 0 0 9px #23232396;
  margin-top: 30px;
  padding: 30px;
}

.weather .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.weather .container .leftTitle {
  font-family: "Columbina", sans-serif;
  font-size: 30pt;
}

.weather .container .flex-weather {
  flex-direction: row-reverse;
  display: flex;
  align-items: center;
}

.weather .container .status.sun {
  background-image: url(data:image/svg+xml;utf8;base64,);
  width: 85px;
  height: 85px;
  background-size: cover;
}

.weather .container .weather-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  margin-right: 15px;
}

.weather .container .weather-info span:nth-child(2) {
  font-size: 16pt;
}

.weather .container .weather-info span:nth-child(3) {
  font-size: 10pt;
}

.weather .container .weather-info span:nth-child(1) {
  font-size: 29pt;
  margin-bottom: -8px;
}

.news h2 {
  text-align: center;
  font-family: "columbina", sans-serif;
  font-size: 30pt;
  margin: 24px 0 19px;
  color: #256d33;
}

.news .news-blocks {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
  margin-top: 32px;
}

.news .news-blocks .news-block {
  width: 403px;
  height: 400px;
  background: #725d3e;
  padding: 30px;
  box-sizing: border-box;
  font-family: "NotoSans", sans-serif;
  text-decoration: none;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.news .news-blocks .news-block:hover span.img {
  transform: scale(1.05);
}

.news .news-blocks .news-block .time {
  display: block;
  font-size: 13pt;
  font-family: "Calibri", sans-serif;
  font-style: italic;
  margin-top: 15px;
}

.news .news-blocks .news-block span {
  color: #fff;
  font-size: 23pt;
  line-height: 34px;
  position: relative;
}

.news .news-blocks .news-block span.img {
  width: inherit;
  height: 298px;
  display: block;
  background-size: cover;
  background-position: center;
  margin: -30px;
  transition: 0.5s ease;
}

.news .news-blocks .news-block span.img:before {
  content: "";
  background: linear-gradient(0deg, #725d3e 0%, transparent 102%);
  width: inherit;
  height: 101%;
  display: block;
}

.news .news-blocks .news-sliced {
  width: 390px;
  height: 400px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.news .news-blocks .news-sliced .news-block-sliced {
  width: 403px;
  height: 185px;
  background: #725d3e;
  padding: 30px;
  box-sizing: border-box;
  font-family: "NotoSans", sans-serif;
  text-decoration: none;
  display: flex;
  flex-direction: row;
  overflow: hidden;
}

.news .news-blocks .news-sliced .news-block-sliced:hover span.img {
  transform: scale(1.05);
}

.news .news-blocks .news-sliced .news-block-sliced span.img {
  height: inherit;
  display: block;
  background-size: cover;
  background-position: center;
  margin: -30px;
  width: 75%;
  transition: 0.5s ease;
}

.news .news-blocks .news-sliced .news-block-sliced span.img:before {
  content: "";
  background: linear-gradient(270deg, #725d3e 0, transparent 102%);
  width: 101%;
  height: inherit;
  display: block;
}

.news .news-blocks .news-sliced .news-block-sliced > div {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: inherit;
  padding: 30px;
  box-sizing: border-box;
  margin-top: -30px;
}

.news .news-blocks .news-sliced .news-block-sliced > div > span {
  color: #fff;
  font-size: 20pt;
  line-height: 34px;
  position: relative;
}

.news .news-blocks .news-sliced .news-block-sliced > div > span.time {
  display: block;
  font-size: 13pt;
  font-family: Calibri, sans-serif;
  font-style: italic;
  margin-top: 15px;
}

footer {
  background: #256d33;
  margin-top: 35px;
  padding: 25px;
}

footer .container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

footer .container .footer-logo {
  width: 343px;
  height: 68px;
  background: url(../img/logo.png) 0 -81px no-repeat;
  text-indent: -9999px;
  transition: .2s ease;
  display: block;
}

footer .container .copy span {
  color: #fff;
  font-family: "Calibri", sans-serif;
  font-weight: bold;
  margin-top: 14px;
  display: block;
  font-size: 12pt;
}

footer .container .schs {
  display: flex;
}

footer .container .schs > .sch1 {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  height: 75px;
  margin-left: 13px;
}

footer .container .schs > .sch1 > span {
  width: 88px;
  height: 31px;
  background: #479056;
  display: block;
}

.moreworks {
  background: #256d34;
  font-family: "NotoSans", sans-serif;
  font-size: 19pt;
  color: #fff;
  text-align: center;
  padding: 19px;
  border-bottom: 3px solid #62984d;
  margin: 13px;
  cursor: pointer;
  transition: 0.2s ease;
}

.moreworks:hover {
  filter: brightness(1.15);
}

.like {
  z-index: 6;
  position: relative;
  font-family: "NotoSans", sans-serif;
  font-size: 15pt;
  padding: 4px 16px;
  float: right;
  color: #9c9c9c;
  cursor: pointer;
  transition: 0.2s ease;
}

.like:before {
  background-image: url(data:image/svg+xml;utf8;base64,);
  content: " ";
  width: 18px;
  height: 18px;
  display: inline-block;
  background-size: cover;
  vertical-align: -2px;
  margin-right: 6px;
  transition: 0.2s ease;
}

.like:hover {
  color: #b72156;
}

.like:hover:before {
  background-image: url(data:image/svg+xml;utf8;base64,);
}

.like.active {
  color: #b72156;
}

.like.active:before {
  background-image: url(data:image/svg+xml;utf8;base64,);
}

.subtitle {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 19px;
}

.left-inf {
  display: flex;
}

.ava {
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #7fb565;
  font-family: "Columbina";
  font-size: 22pt;
  color: #fff;
  text-shadow: 0 0 14px #0000000f;
  border-radius: 50%;
  margin-right: 12px;
}

.win-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  font-family: "NotoSans", sans-serif;
  font-size: 14pt;
}

.win-info span {
  font-family: "Calibri", sans-serif;
  font-size: 11pt;
  font-style: italic;
  margin-top: -4px;
  color: #ababab;
  display: block;
}

.window-content .like {
  padding-right: 0;
}

.win-photo img {
  max-width: 630px;
}

.addcomment {
  border-top: 1px solid rgba(161, 161, 161, 0.12);
  margin-top: 15px;
  padding-top: 15px;
}

.addcomment textarea {
  width: 100%;
  max-width: 100%;
  min-width: 100%;
  outline: none;
  display: block;
  position: relative;
  border: 1px solid #f5f5f5;
  border-bottom: 2px solid #2c2e38;
  box-shadow: 0 0 9px rgba(45, 46, 56, 0);
  transition: 0.3s ease;
  padding: 10px;
  box-sizing: border-box;
  min-height: 150px;
}

.addcomment form {
  margin-top: 13px;
}

h3 {
  font-family: "NotoSans", sans-serif;
  text-align: left;
  font-size: 14pt;
}

input[type="text"],
input[type="email"] {
  border: 1px solid #f5f5f5;
  border-bottom: 2px solid #2d2e38;
  padding: 10px;
}

.flex-inputs {
  display: flex;
  justify-content: space-between;
  margin-top: 18px;
}

.flex-inputs input {
  padding: 10px;
  outline: none;
  width: 191px;
}

input.submit-btn {
  border: none;
  background: #287b26;
  font-family: "NotoSans", sans-serif;
  color: #fff;
  width: 170px;
  font-size: 13pt;
  cursor: pointer;
  transition: 0.2s ease;
}

input.submit-btn:hover {
  filter: brightness(1.15);
}

.remodal.remodal-is-initialized.remodal-is-opened {
  padding-bottom: 10px;
}

.all-comments {
  border-top: 1px solid rgba(161, 161, 161, 0.12);
  margin-top: 15px;
  padding-top: 15px;
}

.comment-block {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
  border-bottom: 1px solid rgba(161, 161, 161, 0.12);
  padding-bottom: 10px;
}

.comment-block:last-child {
  border: none;
  margin-bottom: 0;
}

.com-content {
  text-align: left;
}

.com-info {
  font-family: "NotoSans", sans-serif;
}

.com-info span:last-child {
  font-family: "Calibri", sans-serif;
  font-size: 10pt;
  color: #c1c1c1;
  font-style: italic;
  margin-left: 10px;
  border-left: 1px solid #e4e4e4;
  padding-left: 10px;
}

.com-info .author-label {
  font-family: "Calibri", sans-serif;
  color: #fff;
  background: #287b26;
  padding: 2px 10px;
  font-size: 9pt;
  border-radius: 4px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.filters {
  display: flex;
  justify-content: space-between;
  padding: 13px;
  align-items: center;
  margin-bottom: 10px;
}

.filters .fils {
  color: #333;
  font-family: "NotoSans", sans-serif;
}

.filters .fils a {
  margin-left: 10px;
  border-left: 1px solid #555;
  padding-left: 10px;
  text-decoration: none;
  color: #929292;
  cursor: pointer;
  transition: 0.2s ease;
}

.filters .fils a.active,
.filters .fils a:hover {
  color: #248001;
}

.filters .fils a:first-child {
  border: none;
  margin: 0;
}

.filters .add-btn {
  background: #256d34;
  font-family: "NotoSans", sans-serif;
  font-size: 12pt;
  color: #fff;
  text-decoration: none;
  text-align: center;
  padding: 10px 30px;
  border-bottom: 3px solid #62984d;
  cursor: pointer;
  transition: .2s ease;
}

.filters .add-btn:hover {
  filter: brightness(1.15);
}

input.file {
  border: 1px dashed #dcdcdc;
  padding: 23px;
  width: 581px;
}

.addphoto {
  border: none;
  margin-top: -20px;
  padding-bottom: 22px;
}

@media screen and (max-width: 780px) {
  .container {
    padding-left: 15px;
    padding-right: 15px;
  }
}